import { useEffect, useState } from 'react';
import { UserListApi } from '../../utils/adminApi';
import { Table, Typography } from 'antd';
const { Column } = Table;

const Gifts = () => {
  const [tableData, setTableData] = useState();

  useEffect(() => {
    UserListApi().then((res) => {
      setTableData(
        res.data.map((item) => ({
          ...item,
          _id: item._id.slice(0, 7),
          createdAt: item.createdAt.replace(/[TZ]/g, ' '),
          updatedAt: item.updatedAt.replace(/[TZ]/g, ' '),
        }))
      );
    });
  }, []);

  const ban = () => {};
  const edit = () => {};
  const recharge = () => {};
  const del = () => {};

  return (
    <Table dataSource={tableData} rowKey="_id" className="border-green-600">
      <Column title="序号" dataIndex="_id" className="border-green-600" />
      <Column title="用户" dataIndex="nick" className="border-green-600" />
      <Column title="种类" dataIndex="kind" className="" />
      <Column title="时间" dataIndex="createdAt" className="" />
    </Table>
  );
};

export default Gifts;
